import stepOne from './step1.vue';
import stepTwo from './step2.vue';
import stepThree from './step3.vue';
import stepFour from './step4.vue';
import { mapState,mapGetters } from 'vuex';
export default {
	data() {
		return {
			numList: [
				{
					name: '任务信息'
				},
				{
					name: '招募信息'
				},
				{
					name: '人员招募筛选'
				},
				{
					name: '开始生产'
				}
			],
			form_title:['任务基本信息','招募信息','任务需求'],
			num: 0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			task_id:'',
			route_id:'',
			loading:true,
			recruiting_info:{},
			show_modal:false,
		};
	},
	components: {
		stepOne,
		stepTwo,
		stepThree,
		stepFour
	},
	onLoad(option) {
		console.log(getCurrentPages())
		console.log(option.id)
		this.route_id = option.id
		if(this.route_id){
			let status = this.current_task.status
			if( status == '0'){
				this.num = 1
			}else if(status == '1'){
				this.num = 2
			}
			
		}
		this.loading = false
	},
	computed:{
		...mapState(['current_task']),
	},
	methods: {
		next(){
			switch (this.num){
				case 0:
					this.$refs.stepOne.formSubmit((id) =>{
						
						this.num++
						this.task_id = id
					})
					break;
				case 1:
					this.$refs.stepTwo.formSubmit(() =>{
						
						this.num++
					})
					break;
				default:
					break;
			}
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		prev(){
			this.num--
		},
		scroll(e){
			// console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		update_recruiting_info(data){
			this.recruiting_info = data
		},
		endCreate(e){
			let pamars = {
				proId:this.recruiting_info.proId,
				recId:this.recruiting_info.id,
				formId:e.detail.formId,
				path:'pages/tabbar/task/task',
			}
			console.log(pamars)
			// return 
			this.$http.post('personwx.finishrecruit/1.0/',pamars).then(res =>{
				console.log(res)
				this.scrollTop = this.old.scrollTop
				this.$nextTick(function() {
					this.scrollTop = 0
				});
				this.num ++ 
				// this.$store.commit('setHasTask',true)
			})
		},
		goto_recruiting(){
			uni.navigateTo({
				url: `/pages/personList/personList?recId=${this.recruiting_info.id}`
			});
		},
	}
};
